 * Replace them with your own styles or remove the file completely.
 */
 @import "boot";

 #raviol-form {
	label {
		font-size: 15px !important;
		color: #76787b;
		border: 2px solid transparent;
	}

	.form-control {
		border: 2px solid #dfe3eb !important;
		border-radius: 0 !important;
	}

	textarea {
		height: 100px;
		min-height: 285px;
		position: relative;
		padding-top: 45px;
		height: 100px;
	}

	.textarea-before {
		position: absolute;
		display: block;
		width: 100%;
		height: 40px;
		background-color: #dfe3eb;
		z-index: 9;
	}
 }
.wp-block-ptqblock {
	&__featured-image {
		margin-bottom: 0.5em;

		&.alignleft {
			margin-right: 1em;
		}

		&.alignright {
			margin-left: 1em;
		}

		&.aligncenter {
			text-align: center;
		}
	}

	&__meta,
	&__content {
		margin-top: 0.5em;
	}

	&__meta-item {
		display: flex;
		flex-flow: row wrap;
		gap: 0.5em;
		font-size: 0.9rem;
	}

	&__meta-description {
		margin: 0;
	}

	&__list {
		&:not(.has-list-markers) {
			list-style-type: none;
			padding-left: 0;
		}

		&.has-featured-image {
			.wp-block-ptqblock {
				&__item {
					clear: both;
				}
			}
		}

		&.has-dates,
		&.has-author,
		&.has-featured-image,
		&.has-excerpt {
			.wp-block-ptqblock {
				&__item {
					&:not(:last-child) {
						margin-bottom: 1em;
					}
				}
			}
		}

		&.is-grid {
			display: grid;
			gap: 2em;
			list-style-type: none;
			margin-left: 0;
			padding-left: 0;
		}

		@for $i from 1 through 10 {
			&.columns-#{$i} {
				grid-template-columns: repeat(#{$i}, minmax(0, 1fr));
			}
		}
	}

	.screen-reader-text {
		border: 0;
		clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
		clip: rect(1px, 1px, 1px, 1px);
		width: 1px;
		height: 1px;
		overflow: hidden;
		padding: 0;
		position: absolute !important;
		word-break: normal;
		word-wrap: normal !important; /* Many screen reader and browser combinations announce broken words as they would appear visually. */
	}
}
